
.container {
    text-align: center;
    margin: 20px;
    max-width: 100%; 
    padding: 0 10px;
  }
  

  .game-management {
    display: flex;
    justify-content: space-between;
    margin-bottom: 30px;
    padding: 10px 0;
  }
  

  .game-management button, 
  .controls button {
    padding: 10px 20px;
    font-size: 18px;
    background-color: #ffeb3b;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    margin: 5px;
  }
  
  button:hover {
    background-color: #ffd600;
  }
  
  
  .letter-grid {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    gap: 10px;
    margin-bottom: 20px;
  }
  
  .letter {
    width: 60px;
    height: 60px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    background-color: lightgray;
    font-size: 24px;
    font-weight: bold;
    color: #333;
    cursor: pointer;
  }
  
  .letter.central {
    background-color: #ffeb3b;
    color: black;
  }
  

  input[type="text"] {
    padding: 10px;
    font-size: 18px;
    width: 250px;
    margin-bottom: 20px;
    border-radius: 5px;
    border: 1px solid #ccc;
  }
  
 
  @media (max-width: 768px) {
    .letter {
      width: 50px;
      height: 50px;
      font-size: 20px;
    }
  
    input[type="text"] {
      width: 200px;
    }
  
    .controls button {
      font-size: 16px;
      padding: 8px 16px;
    }
  }
  
 
  .word-info {
    font-size: 18px;
    color: #333;
    margin-bottom: 20px;
  }
  

  .found-words-container {
    margin-top: 10px;
    padding: 15px;
    border: 1px solid #ccc;
    border-radius: 5px;
    background-color: #f7f7f7;
    text-align: left;
    margin-bottom: 20px;
    height: 150px;
    overflow-y: auto; 
  }
  
  .found-words-container h2 {
    margin-bottom: 10px;
    font-size: 20px;
  }
  
 
  .word-list {
    list-style-type: none;
    padding-left: 0;
    font-size: 16px;
  }
  
  .word-list li {
    padding: 5px 0;
    border-bottom: 1px solid #ddd;
    color: #555;
  }
  

  .controls {
    margin-top: 20px;
  }
  

  .score {
    font-size: 24px;
    color: #333;
  }


.word-input {
  color: black; 
  transition: color 0.3s ease; 
}


@keyframes shake {
  0% { transform: translateX(0); }
  25% { transform: translateX(-5px); }
  50% { transform: translateX(5px); }
  75% { transform: translateX(-5px); }
  100% { transform: translateX(0); }
}

.invalid-word {
  animation: shake 0.3s ease;
  color: red; 
}


@keyframes celebrate {
  0% { transform: scale(1); }
  50% { transform: scale(1.1); }
  100% { transform: scale(1); }
}

.valid-word {
  animation: celebrate 0.5s ease;
  color: green; 
}
